<template>
<div style="padding: 0 16px 0 16px">
  <h4 style="font-family: 'Roboto', sans-serif;font-weight: 300">Your delivery status already update</h4>
  <v-row class="mt-5">
    <v-col>
        <v-img src="@/assets/images/succeed.svg" width="160px" height="160px">
        </v-img>
    </v-col>
  </v-row>
  <v-row style="margin-top: 10px">
    <v-col>
      <span>Redirect to personal center after {{ wait_time }} seconds. . .</span>
    </v-col>
  </v-row>
  <v-row>
    <v-col>
      <v-btn color="#c31126" dark depressed block x-large @click="goto_au_post">
        GO NOW
      </v-btn>
    </v-col>
  </v-row>
</div>
</template>

<script>
export default {
  name: "ThanksView",
  data: () => ({
    wait_time: 3
  }),
  methods: {
    time_reduce() {
      if (this.wait_time > 0) {
        this.wait_time = this.wait_time - 1
      } else {
        window.location.href = "https://www.singpost.com/"
      }
    },
    goto_au_post() {
      window.location.href = "https://www.singpost.com/"
    },
  },
  mounted() {
    setInterval(this.time_reduce, 1000)
  }
}
</script>

